
.wrapper{
    width: 33%;
    background-color: #fff;
    padding: 25px;
   
    border-radius: 6px;
    box-shadow: 0px 0px 10px -2px #333;
    
}
@media(max-width: 576px){
   .wrapper{
       width: 80%;
   }
}

h3{
   
    font-weight: normal;
    text-transform: none;
    margin-bottom: 2rem;
    font-size: 26px;
}

.ul_persons{

}

.li_person{
    width: 100%;
    display: flex;
    
    align-items:center;
    margin-bottom: 1.5rem;
}

.li_person img{
    width: 75px;
    height: 75px;
    border-radius: 50%;
    object-fit: cover;
    /* box-shadow: 0px 5px 15px rgba(0,0,0,0.4); */
    margin-right: 15px;
   
}

.li_person h4{
    margin-bottom: 0.35rem;
    
}

.li_person p{
    color: hsl(210, 22%, 49%);
}

.wrapper button {
    background-color: #f28ab2;
    color: #fff;
    width: 100%;
    border-radius: 6px;
    padding: 12px;
    cursor: pointer;
    font-size: 16px;
}

.wrapper button:hover{
    opacity: 0.9;
}